export const required = value => (value || typeof value === 'number' ? undefined : '')

export const checkLogin = value => (value && /^[a-z]+([-_]?[a-z0-9]+){0,2}$/i.test(value) ? undefined : ' ')

export const checkEmail = value => (value && /^[0-9a-z_-]+@[0-9a-z_-]+\.[a-z]{2,5}$/i.test(value) ? undefined : ' ')

export const checkPassword = value => (value && /^[0-9a-z#!_-]/i.test(value) ? undefined : ' ')

export const passwordsMatch = (value, allValues) =>
        value !== allValues.password ? '  ' : undefined;
